.list-box{
    margin-top: 12px;
    width: 100%;
    .list-ul{
        .InfiniteScroll{
            width: 100%;
            margin: 0 auto;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-evenly;
            row-gap: 28px;
            .InfiniteScroll::after{
                display: inline-block;
                content: '';
                flex: auto;
            }
            >li{
                width: 21%;
                background-color: #ebebeb;
                padding-bottom: 25px;
                figure{
                    width: 100%;
                    margin: 0;
                    display: flex;
                    align-items: center;
                    flex-direction: column;
                    row-gap: 8px;
                    img{
                        width: 200px;
                    }
                    figcaption{
                        width: 100%;
                        font-size: 14px;
                        display: flex;
                        justify-content: center;
                    }
                    &>figcaption:nth-of-type(1){
                        color: #c42a16;
                        font-weight: 700;
                        font-size: 18px;
                        justify-content: center;
                    }
                    &>figcaption:nth-of-type(2){
                        .icon-ul{
                            li{
                                text-align: center;
                            }
                        }
                        .msg-ul{
                            padding-left: 12px;
                            li:nth-child(1){
                                font-weight: 700;
                                a{
                                    color: #2c2c2c;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    @media screen and (max-width:992px) {
        .list-ul{
            .InfiniteScroll{
                row-gap:7px;
                >li{
                    width:47%;
                    figure{
                        width: 100%;
                        row-gap: 4px;
                        img{
                            width: 50%;
                        }
                        figcaption{
                            font-size: 0.6rem;
                        }
                        &>figcaption:nth-of-type(1){
                            font-size: 0.8rem;
                        }
                        .msg-ul{
                            text-align: left;
                            padding-left: 4px !important;
                        }
                    }
                }
            }
        }
    }
}