.table-ul{
    margin: 12px auto;
    display: flex;
    justify-content: space-evenly;
    font-size: 16px;
    >li{
        cursor: pointer;
        user-select: none;
        a{
            &:hover{
                color: #c42a16;
            }
            color: black;
        }
        .handle{
            color: #c42a16;
            font-weight: 700;
        }
    }
}
@media screen and (max-width:992px) {
    .table-ul{
        margin: 0.3rem auto;
        font-size: 0.7rem;
    }
}

